import {Component, OnInit} from '@angular/core';
import {BaseApiService} from '../shared/api-service/base-api.service';
import {DialogService} from '../shared/dialog-service/dialog.service';
import {Face} from '../models/face';
import {Ng2FileDropAcceptedFile} from 'ng2-file-drop';

@Component({
    moduleId: module.id,
    selector: 'app-recognize',
    templateUrl: './recognize.component.html',
    styleUrls: ['./recognize.component.scss'],
    providers: [BaseApiService, DialogService],
    entryComponents: []
})
export class RecognizeComponent {
    public faces: Array<Face> = [];
    private supportedFileTypes: string[] = ['image/png', 'image/jpeg', 'image/gif'];
    private imageShown = false;
    private currentProfileImage = 'assets/images/grafty-logo.png';
    constructor(private apiService: BaseApiService, private dialogService: DialogService) {
    }
    private dragFileAccepted(acceptedFile: Ng2FileDropAcceptedFile) {
        const fileReader = new FileReader();
        fileReader.onloadend = () => {
            this.currentProfileImage = fileReader.result;
            this.imageShown = true;
            const body = {
                command: 'fr-test',
                image: fileReader.result.substr(23)
            };
            this.apiService.frPost('test', body).subscribe(response => {
                console.log('Response' , response);
                this.addToArray(JSON.parse(response._body).faces[0] , fileReader.result);
            }, error => {
                console.error('Error', error);
            });
        };
        fileReader.readAsDataURL(acceptedFile.file);
    }
    private openImageComponent(src: any, x: any, y: any, w: any, h: any) {
        this.dialogService.showImage(src, x, y, w, h);
    }

    private addToArray(face: Face, result: any) {
        face.src = result;
        this.faces = [];
        this.faces.push(face);
    }
}



